import React, { useEffect, useState } from 'react';
import { X, ChevronLeft, ChevronRight, Sparkles, Gem, PenTool } from 'lucide-react';
import { useAuth } from '../context/AuthContext';
import axios from 'axios';
import JourneyStories from '../components/JourneyStories';
import DesignGallery from '../components/DesignGallery';

const API_URL = process.env.REACT_APP_BACKEND_URL + '/api';

// View modes for the gallery
const VIEW_MODES = [
  { id: 'gallery', name: 'Galleries', icon: Sparkles },
  { id: 'journeys', name: 'Journeys', icon: Gem },
  { id: 'designs', name: 'Designs', icon: PenTool },
];

const CATEGORIES = [
  { id: 'all', name: 'All Gemstones' },
  { id: 'sapphire', name: 'Sapphire' },
  { id: 'tourmaline', name: 'Tourmaline' },
  { id: 'emerald', name: 'Emerald' },
  { id: 'aquamarine', name: 'Aquamarine' },
  { id: 'garnet', name: 'Garnet' },
  { id: 'other', name: 'Other' },
];

const ERAS = [
  { id: 'all', name: 'All Eras' },
  { id: 'PAST', name: 'Past' },
  { id: 'PRESENT', name: 'Present' },
];

// Humble Beginnings Section - ONLY renders for eligible users
const HumbleBeginningsSection = ({ items, entitlements, isAuthenticated, onItemClick }) => {
  const humbleItems = items.filter(item => item.humble_beginnings);
  
  // G1: Do NOT render at all for ineligible users
  const isUnlocked = isAuthenticated && entitlements.unlocked_nyp;
  if (!isUnlocked || humbleItems.length === 0) {
    return null;
  }
  
  return (
    <div className="mb-12" data-testid="humble-beginnings-section">
      <div className="flex items-center gap-3 mb-6">
        <Sparkles className="w-5 h-5 text-amber-400" />
        <h2 className="text-xl title-sm text-amber-400">Humble Beginnings</h2>
      </div>
      
      <p className="text-amber-400/80 text-sm mb-6" data-testid="humble-unlocked-msg">
        A curated look at where it all started.
      </p>
      
      <div className="gallery-grid">
        {humbleItems.map((item, index) => (
          <div
            key={item.id}
            onClick={() => onItemClick(index)}
            className="group relative aspect-square overflow-hidden gem-card cursor-pointer opacity-0 animate-fade-in"
            style={{ animationDelay: `${index * 50}ms` }}
            data-testid={`humble-item-${index}`}
          >
            <img
              src={item.image_url}
              alt={item.title}
              className="w-full h-full object-cover transition-all duration-700 group-hover:scale-110"
            />
            <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
            <div className="absolute bottom-0 left-0 right-0 p-4 translate-y-4 opacity-0 group-hover:translate-y-0 group-hover:opacity-100 transition-all duration-300">
              <p className="spec-text text-amber-400/80 mb-1">Humble Beginnings</p>
              <h3 className="title-sm text-base">{item.title}</h3>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

const Gallery = () => {
  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [selectedEra, setSelectedEra] = useState('all');
  const [viewMode, setViewMode] = useState('gallery');
  const [lightboxOpen, setLightboxOpen] = useState(false);
  const [lightboxIndex, setLightboxIndex] = useState(0);
  const [expandedMobileItem, setExpandedMobileItem] = useState(null);
  const { isAuthenticated, entitlements } = useAuth();

  useEffect(() => {
    if (viewMode === 'gallery') {
      fetchGallery();
    }
  }, [selectedCategory, selectedEra, viewMode]);

  const fetchGallery = async () => {
    try {
      setLoading(true);
      let url = `${API_URL}/gallery`;
      const params = [];
      if (selectedCategory !== 'all') params.push(`category=${selectedCategory}`);
      if (selectedEra !== 'all') params.push(`era=${selectedEra}`);
      if (params.length > 0) url += `?${params.join('&')}`;
      
      const response = await axios.get(url);
      setItems(response.data);
    } catch (error) {
      console.error('Failed to fetch gallery:', error);
    } finally {
      setLoading(false);
    }
  };

  // Filter non-humble items for regular grid
  const regularItems = items.filter(item => !item.humble_beginnings);
  const humbleItems = items.filter(item => item.humble_beginnings);

  // Desktop lightbox functions
  const openLightbox = (index, isHumble = false) => {
    const sourceItems = isHumble ? humbleItems : regularItems;
    const actualIndex = items.findIndex(item => item.id === sourceItems[index]?.id);
    setLightboxIndex(actualIndex >= 0 ? actualIndex : index);
    setLightboxOpen(true);
    document.body.style.overflow = 'hidden';
  };

  const closeLightbox = () => {
    setLightboxOpen(false);
    document.body.style.overflow = 'auto';
  };

  const navigateLightbox = (direction) => {
    if (direction === 'prev') {
      setLightboxIndex(prev => (prev === 0 ? items.length - 1 : prev - 1));
    } else {
      setLightboxIndex(prev => (prev === items.length - 1 ? 0 : prev + 1));
    }
  };

  // Mobile item toggle
  const toggleMobileItem = (itemId) => {
    setExpandedMobileItem(prev => prev === itemId ? null : itemId);
  };

  // Keyboard navigation for desktop
  useEffect(() => {
    const handleKeyDown = (e) => {
      if (!lightboxOpen) return;
      if (e.key === 'Escape') closeLightbox();
      if (e.key === 'ArrowLeft') navigateLightbox('prev');
      if (e.key === 'ArrowRight') navigateLightbox('next');
    };
    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [lightboxOpen, items.length]);

  const currentItem = items[lightboxIndex];

  return (
    <div className="min-h-screen" data-testid="gallery-page">
      {/* Header */}
      <section className="section-spacing pb-16">
        <div className="container-custom">
          <p className="text-sm uppercase tracking-[0.2em] text-gray-500 mb-4">Portfolio</p>
          <h1 className="page-title title-xl">Portfolio</h1>
        </div>
      </section>

      {/* Mobile View Mode Toggle */}
      <div className="md:hidden sticky top-20 z-30 bg-black/95 backdrop-blur-md border-b border-white/10 px-4 py-4">
        <div className="flex gap-2">
          {VIEW_MODES.map(mode => {
            const Icon = mode.icon;
            const isActiveMode = viewMode === mode.id;
            return (
              <button
                key={mode.id}
                onClick={() => setViewMode(mode.id)}
                className={`flex-1 flex items-center justify-center gap-2 py-3 text-sm font-medium transition-all border ${
                  isActiveMode 
                    ? 'bg-amber-500/20 border-amber-500 text-amber-400' 
                    : 'border-white/20 text-gray-400 hover:border-teal-500/50 hover:text-teal-400'
                }`}
                data-testid={`mobile-view-${mode.id}`}
              >
                <Icon className={`w-4 h-4 ${isActiveMode ? 'text-amber-400' : ''}`} />
                <span>{mode.name}</span>
              </button>
            );
          })}
        </div>
        
        {/* Mobile Category/Era Filter for Gallery view */}
        {viewMode === 'gallery' && (
          <div className="mt-4 flex gap-2 overflow-x-auto pb-2 -mx-1 px-1 scrollbar-hide">
            {ERAS.filter(e => e.id !== 'all').map(era => (
              <button
                key={era.id}
                onClick={() => setSelectedEra(era.id === selectedEra ? 'all' : era.id)}
                className={`flex-shrink-0 px-4 py-2 text-xs font-medium border transition-all ${
                  selectedEra === era.id
                    ? 'bg-teal-500/20 border-teal-500 text-teal-400'
                    : 'border-white/20 text-gray-500 hover:border-teal-500/50'
                }`}
              >
                {era.name}
              </button>
            ))}
            {CATEGORIES.filter(c => c.id !== 'all').map(cat => (
              <button
                key={cat.id}
                onClick={() => setSelectedCategory(cat.id === selectedCategory ? 'all' : cat.id)}
                className={`flex-shrink-0 px-4 py-2 text-xs font-medium border transition-all ${
                  selectedCategory === cat.id
                    ? 'bg-amber-500/20 border-amber-500 text-amber-400'
                    : 'border-white/20 text-gray-500 hover:border-amber-500/50'
                }`}
              >
                {cat.name}
              </button>
            ))}
          </div>
        )}
      </div>

      {/* Mobile Gallery/Journeys/Designs */}
      <section className="pb-24 md:hidden">
        <div className="px-4 pt-4">
          {viewMode === 'journeys' ? (
            <JourneyStories />
          ) : viewMode === 'designs' ? (
            <DesignGallery />
          ) : (
            <>
              {loading ? (
                <div className="grid grid-cols-2 gap-4">
                  {[...Array(6)].map((_, i) => (
                    <div key={i} className="aspect-square bg-white/5 animate-pulse" />
                  ))}
                </div>
              ) : items.length === 0 ? (
                <div className="text-center py-24">
                  <p className="text-gray-500">No items in gallery.</p>
                </div>
              ) : (
                <div className="grid grid-cols-2 gap-4">
                  {items.map((item, index) => (
                    <div
                      key={item.id}
                      onClick={() => toggleMobileItem(item.id)}
                      className="relative aspect-square overflow-hidden cursor-pointer opacity-0 animate-fade-in border border-white/10"
                      style={{ animationDelay: `${index * 50}ms` }}
                      data-testid={`mobile-gallery-item-${index}`}
                    >
                      {/* Image or Info overlay */}
                      {expandedMobileItem === item.id ? (
                        <div className="absolute inset-0 bg-black/95 flex flex-col items-center justify-center p-4 text-center z-10">
                          <p className="text-xs uppercase tracking-widest text-amber-400 mb-2">{item.category}</p>
                          <h3 className="title-sm text-sm mb-3">{item.title}</h3>
                          <div className="space-y-1">
                            {item.gemstone_type && (
                              <p className="text-xs text-gray-400">Gem Type: <span className="text-teal-400">{item.gemstone_type}</span></p>
                            )}
                            {item.color && (
                              <p className="text-xs text-gray-400">Color: <span className="text-teal-400">{item.color}</span></p>
                            )}
                            {item.carat && (
                              <p className="text-xs text-gray-400">Weight: <span className="text-teal-400">{item.carat}</span></p>
                            )}
                          </div>
                          <p className="text-[10px] text-gray-600 mt-4">Tap to close</p>
                        </div>
                      ) : (
                        <>
                          <img
                            src={item.image_url}
                            alt={item.title}
                            className="w-full h-full object-cover"
                          />
                          {/* Mobile item label */}
                          <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/80 to-transparent p-3">
                            <p className="text-[10px] text-amber-400 uppercase tracking-wider">{item.era}</p>
                          </div>
                        </>
                      )}
                    </div>
                  ))}
                </div>
              )}
            </>
          )}
        </div>
      </section>

      {/* Desktop Gallery Layout - unchanged */}
      <section className="pb-24 hidden md:block">
        <div className="container-custom">
          <div className="grid grid-cols-1 md:grid-cols-[240px_1fr] gap-8">
            {/* Sidebar */}
            <aside className="md:sticky md:top-28 md:h-fit" data-testid="gallery-sidebar">
              {/* View Mode Toggle */}
              <h3 className="text-sm uppercase tracking-widest text-gray-500 mb-4">View</h3>
              <nav className="space-y-1 mb-8">
                {VIEW_MODES.map(mode => {
                  const Icon = mode.icon;
                  const isActive = viewMode === mode.id;
                  return (
                    <button
                      key={mode.id}
                      onClick={() => setViewMode(mode.id)}
                      className={`category-item w-full text-left flex items-center gap-2 ${isActive ? 'active' : ''}`}
                      data-testid={`view-${mode.id}`}
                    >
                      <Icon className={`w-4 h-4 ${isActive ? 'text-amber-400' : ''}`} />
                      {mode.name}
                    </button>
                  );
                })}
              </nav>

              {/* Era Filter - only show for gallery view */}
              {viewMode === 'gallery' && (
                <>
                  <h3 className="text-sm uppercase tracking-widest text-gray-500 mb-4">Era</h3>
                  <nav className="space-y-1 mb-8">
                    {ERAS.map(era => {
                      const isActive = selectedEra === era.id;
                      return (
                        <button
                          key={era.id}
                          onClick={() => setSelectedEra(era.id)}
                          className={`category-item block w-full text-left ${isActive ? 'active !text-amber-400' : ''}`}
                          data-testid={`era-${era.id}`}
                        >
                          {era.name}
                        </button>
                      );
                    })}
                  </nav>
                  
                  {/* Category Filter */}
                  <h3 className="text-sm uppercase tracking-widest text-gray-500 mb-4">Gem Type</h3>
                  <nav className="space-y-1">
                    {CATEGORIES.map(cat => {
                      const isActive = selectedCategory === cat.id;
                      return (
                        <button
                          key={cat.id}
                          onClick={() => setSelectedCategory(cat.id)}
                          className={`category-item block w-full text-left ${isActive ? 'active !text-amber-400' : ''}`}
                          data-testid={`category-${cat.id}`}
                        >
                          {cat.name}
                        </button>
                      );
                    })}
                  </nav>
                </>
              )}
            </aside>

            {/* Main Content Area */}
            <div data-testid="gallery-content">
              {viewMode === 'journeys' ? (
                <JourneyStories />
              ) : viewMode === 'designs' ? (
                <DesignGallery />
              ) : (
                /* Gallery Grid */
                <div data-testid="gallery-grid">
                  {loading ? (
                    <div className="gallery-grid">
                      {[...Array(9)].map((_, i) => (
                        <div key={i} className="aspect-square bg-white/5 animate-pulse" />
                  ))}
                </div>
              ) : items.length === 0 ? (
                <div className="text-center py-24">
                  <p className="text-gray-500">No items found in this category.</p>
                </div>
              ) : (
                <>
                  {/* Humble Beginnings Gated Section */}
                  {humbleItems.length > 0 && (
                    <HumbleBeginningsSection
                      items={items}
                      entitlements={entitlements}
                      isAuthenticated={isAuthenticated}
                      onItemClick={(index) => openLightbox(index, true)}
                    />
                  )}
                  
                  {/* Regular Gallery Grid */}
                  {regularItems.length > 0 && (
                    <>
                      {humbleItems.length > 0 && (
                        <h2 className="text-xl title-sm text-white mb-6">Collection</h2>
                      )}
                      <div className="gallery-grid">
                        {regularItems.map((item, index) => (
                          <div
                            key={item.id}
                            className="opacity-0 animate-fade-in"
                            style={{ animationDelay: `${index * 50}ms` }}
                          >
                            <div
                              onClick={() => openLightbox(index)}
                              className="group relative aspect-square overflow-hidden gem-card cursor-pointer mb-4"
                              data-testid={`gallery-item-${index}`}
                            >
                              <img
                                src={item.image_url}
                                alt={item.title}
                                className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-110"
                              />
                              <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
                            </div>
                            <p className="text-sm text-amber-400 uppercase tracking-widest">{item.title}</p>
                          </div>
                        ))}
                      </div>
                    </>
                  )}
                </>
              )}
                </div>
              )}
            </div>
          </div>
        </div>
      </section>

      {/* Desktop Lightbox */}
      {lightboxOpen && currentItem && (
        <div className="fixed inset-0 z-50 lightbox-overlay hidden md:flex items-center justify-center" data-testid="lightbox">
          {/* Close button */}
          <button
            onClick={closeLightbox}
            className="absolute top-6 right-6 text-gray-400 hover:text-white transition-colors z-10"
            data-testid="lightbox-close"
          >
            <X className="w-8 h-8" />
          </button>

          {/* Navigation */}
          <button
            onClick={() => navigateLightbox('prev')}
            className="absolute left-6 top-1/2 -translate-y-1/2 text-gray-400 hover:text-white transition-colors"
            data-testid="lightbox-prev"
          >
            <ChevronLeft className="w-10 h-10" />
          </button>
          <button
            onClick={() => navigateLightbox('next')}
            className="absolute right-6 top-1/2 -translate-y-1/2 text-gray-400 hover:text-white transition-colors"
            data-testid="lightbox-next"
          >
            <ChevronRight className="w-10 h-10" />
          </button>

          {/* Content */}
          <div className="max-w-5xl w-full mx-6 flex flex-col md:flex-row gap-8 items-center">
            <div className="flex-1 max-h-[70vh]">
              <img
                src={currentItem.image_url}
                alt={currentItem.title}
                className="max-w-full max-h-[70vh] object-contain mx-auto"
              />
            </div>
            <div className="w-full md:w-72 text-center md:text-left">
              <p className="spec-text text-gray-500 mb-2">{currentItem.category}</p>
              <h2 className="title-sm text-2xl mb-4">{currentItem.title}</h2>
              {currentItem.description && (
                <p className="text-gray-400 text-sm mb-4">{currentItem.description}</p>
              )}
              <div className="space-y-2">
                {currentItem.carat && (
                  <div className="flex justify-between md:justify-start md:gap-4 text-sm">
                    <span className="text-gray-500">Carat</span>
                    <span className="font-mono">{currentItem.carat}</span>
                  </div>
                )}
                {currentItem.dimensions && (
                  <div className="flex justify-between md:justify-start md:gap-4 text-sm">
                    <span className="text-gray-500">Dimensions</span>
                    <span className="font-mono">{currentItem.dimensions}</span>
                  </div>
                )}
              </div>
              <p className="text-gray-600 text-xs mt-6">
                {lightboxIndex + 1} / {items.length}
              </p>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Gallery;
