import React, { useState, useEffect } from 'react';
import { Package, Image, MessageSquare, Users, DollarSign, ShoppingCart, Receipt, TrendingUp, Clock, Calendar, Handshake } from 'lucide-react';
import { Link } from 'react-router-dom';
import { adminApi } from '../../api/adminApi';

const AdminDashboard = () => {
  const [stats, setStats] = useState({
    products: 0,
    gallery: 0,
    bookings: 0,
    users: 0,
    orders: 0,
    commits: 0,
    negotiations: 0,
    sold: 0,
    inquiries: 0,
    product_inquiries: 0,
    sell_inquiries: 0,
    nyp_inquiries: 0,
    total_revenue: 0,
    pending_bookings: 0,
    pending_inquiries: 0
  });
  const [recentActivity, setRecentActivity] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const fetchDashboardData = async () => {
    try {
      // Fetch stats from dedicated endpoint
      // Recent activity: include_deleted=true to show ALL items
      const [statsData, bookingsData, productInquiriesData, sellInquiriesData, nypInquiriesData, ordersData, negotiationsData] = await Promise.all([
        adminApi.get('/admin/dashboard/stats'),
        adminApi.get('/admin/bookings?include_deleted=true'),
        adminApi.get('/admin/product-inquiries?include_deleted=true'),
        adminApi.get('/admin/sell-inquiries?include_deleted=true'),
        adminApi.get('/admin/name-your-price-inquiries?include_deleted=true'),
        adminApi.get('/admin/orders?include_deleted=true'),
        adminApi.get('/admin/negotiations?include_deleted=true').catch(() => []),
      ]);

      setStats(statsData);

      // Combine and sort recent activity, with deleted + pending flags
      const allActivity = [
        ...bookingsData.map(b => ({ ...b, type: 'booking', typeLabel: 'Booking', activity_is_deleted: !!b.is_deleted, activity_is_pending: b.status === 'pending' })),
        ...ordersData.map(o => ({ ...o, type: 'order', typeLabel: 'Order', activity_is_deleted: !!o.is_deleted, activity_is_pending: o.status === 'pending' })),
        ...productInquiriesData.map(i => ({ ...i, type: 'inquiry', typeLabel: 'Inquiry', activity_is_deleted: !!i.is_deleted, activity_is_pending: i.status === 'pending' })),
        ...sellInquiriesData.map(i => ({ ...i, type: 'sell', typeLabel: 'Sell Inquiry', activity_is_deleted: !!i.is_deleted, activity_is_pending: i.status === 'pending' })),
        ...nypInquiriesData.map(i => ({ ...i, type: 'nyp', typeLabel: 'Name Your Price', activity_is_deleted: !!i.is_deleted, activity_is_pending: i.status === 'pending' })),
        ...negotiationsData.map(n => ({ ...n, created_at: n.updated_at || n.created_at, type: 'negotiation', typeLabel: 'Negotiation', activity_is_deleted: !!n.is_deleted, activity_is_pending: n.status === 'OPEN' })),
      ].sort((a, b) => new Date(b.created_at) - new Date(a.created_at)).slice(0, 12);

      setRecentActivity(allActivity);
    } catch (error) {
      console.error('Failed to fetch dashboard data:', error);
    } finally {
      setLoading(false);
    }
  };

  const statCards = [
    { label: 'Products', value: stats.products, icon: Package, color: 'text-blue-400', path: '/admin/products' },
    { label: 'Gallery Items', value: stats.gallery, icon: Image, color: 'text-purple-400', path: '/admin/gallery' },
    { label: 'Bookings', value: stats.bookings, icon: MessageSquare, color: 'text-green-400', path: '/admin/inquiries' },
    { label: 'Users', value: stats.users, icon: Users, color: 'text-yellow-400', path: '/admin/users' },
    { label: 'Orders', value: stats.orders, icon: ShoppingCart, color: 'text-pink-400', path: '/admin/sold' },
    { label: 'Commits', value: stats.commits, icon: Clock, color: 'text-orange-400', path: '/admin/sold' },
    { label: 'Negotiations', value: stats.negotiations, icon: Handshake, color: 'text-cyan-400', path: '/admin/negotiations' },
  ];

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      month: 'short', day: 'numeric', hour: '2-digit', minute: '2-digit'
    });
  };

  const getActivityDescription = (item) => {
    switch (item.type) {
      case 'booking':
        return `Booking from ${item.name} - ${item.service}`;
      case 'order':
        return `Order - $${item.total?.toLocaleString()}${item.paid_at ? ' (paid)' : ''}`;
      case 'inquiry':
        return `Product inquiry from ${item.name}`;
      case 'sell':
        return `Sell inquiry from ${item.name} - $${item.asking_price}`;
      case 'nyp':
        return `Name Your Price from ${item.name} - $${item.price}`;
      case 'negotiation':
        return `Negotiation ${item.negotiation_id?.slice(0, 8) || ''} - ${item.status || 'OPEN'}`;
      default:
        return 'Activity';
    }
  };

  const getActivityBadgeColor = (type) => {
    switch (type) {
      case 'booking': return 'bg-green-500/20 text-green-400';
      case 'order': return 'bg-blue-500/20 text-blue-400';
      case 'inquiry': return 'bg-yellow-500/20 text-yellow-400';
      case 'sell': return 'bg-purple-500/20 text-purple-400';
      case 'nyp': return 'bg-orange-500/20 text-orange-400';
      case 'negotiation': return 'bg-cyan-500/20 text-cyan-400';
      default: return 'bg-gray-500/20 text-gray-400';
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="w-8 h-8 border-2 border-white/20 border-t-white rounded-full animate-spin" />
      </div>
    );
  }

  return (
    <div data-testid="admin-dashboard">
      <h1 className="page-title title-xl text-3xl mb-8">Dashboard</h1>

      {/* Stats Grid */}
      <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-7 gap-4 mb-8">
        {statCards.map(stat => (
          <Link 
            key={stat.label} 
            to={stat.path}
            className="gem-card p-4 hover:bg-white/5 transition-colors"
            data-testid={`stat-${stat.label.toLowerCase().replace(' ', '-')}`}
          >
            <stat.icon className={`w-6 h-6 ${stat.color} mb-2`} />
            <p className="text-2xl font-mono">{stat.value}</p>
            <p className="text-xs text-gray-500 uppercase tracking-widest">{stat.label}</p>
          </Link>
        ))}
      </div>

      {/* Revenue & Pending Section */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-8">
        <div className="gem-card p-6">
          <div className="flex items-center gap-3 mb-4">
            <TrendingUp className="w-6 h-6 text-green-400" />
            <h2 className="font-semibold">Total Revenue</h2>
          </div>
          <p className="text-3xl font-mono text-green-400" data-testid="total-revenue">
            ${stats.total_revenue?.toLocaleString() || 0}
          </p>
          <p className="text-xs text-gray-500 mt-2">From {stats.sold} sold items</p>
        </div>

        <div className="gem-card p-6">
          <div className="flex items-center gap-3 mb-4">
            <Clock className="w-6 h-6 text-yellow-400" />
            <h2 className="font-semibold">Pending Bookings</h2>
          </div>
          <p className="text-3xl font-mono" data-testid="pending-bookings">{stats.pending_bookings}</p>
          <Link to="/admin/inquiries" className="text-xs text-blue-400 hover:text-blue-300 mt-2 inline-block">
            View all bookings 
          </Link>
        </div>

        <div className="gem-card p-6">
          <div className="flex items-center gap-3 mb-4">
            <DollarSign className="w-6 h-6 text-purple-400" />
            <h2 className="font-semibold">Inquiry Breakdown</h2>
          </div>
          <div className="space-y-1 text-sm">
            <div className="flex justify-between">
              <span className="text-gray-500">Product Inquiries</span>
              <span data-testid="product-inquiries-count">{stats.product_inquiries}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-500">Sell Inquiries</span>
              <span data-testid="sell-inquiries-count">{stats.sell_inquiries}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-500">Name Your Price</span>
              <span data-testid="nyp-inquiries-count">{stats.nyp_inquiries}</span>
            </div>
          </div>
        </div>
      </div>

      {/* Recent Activity */}
      <div className="gem-card p-6">
        <div className="flex items-center justify-between mb-4">
          <h2 className="title-sm text-xl flex items-center gap-2">
            <Calendar className="w-5 h-5 text-gray-400" />
            Recent Activity
          </h2>
          <Link to="/admin/inquiries" className="text-xs text-blue-400 hover:text-blue-300">
            View all 
          </Link>
        </div>
        
        {recentActivity.length === 0 ? (
          <p className="text-gray-500 text-sm">No recent activity</p>
        ) : (
          <div className="space-y-3">
            {recentActivity.map((item, i) => (
              <div 
                key={i} 
                className={`flex items-center justify-between py-3 border-b border-white/5 last:border-0 ${item.activity_is_deleted ? 'opacity-60' : ''}`}
                data-testid={`activity-item-${i}`}
              >
                <div className="flex-1 min-w-0">
                  <p className="text-sm truncate">{getActivityDescription(item)}</p>
                  <p className="text-xs text-gray-500">{formatDate(item.created_at)}</p>
                </div>
                <div className="flex items-center gap-2 ml-3 flex-shrink-0">
                  {item.activity_is_pending && (
                    <span className="text-xs px-2 py-0.5 bg-yellow-500/20 text-yellow-400" data-testid="badge-pending">PENDING</span>
                  )}
                  {item.activity_is_deleted && (
                    <span className="text-xs px-2 py-0.5 bg-red-500/20 text-red-400" data-testid="badge-deleted">DELETED</span>
                  )}
                  <span className={`text-xs px-2 py-1 ${getActivityBadgeColor(item.type)}`}>
                    {item.typeLabel}
                  </span>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default AdminDashboard;
